div.checkbox input[type='checkbox'] {
  position: relative;
  opacity: 0;
  width: 16px;
  height: 16px;
  z-index: 99;
}

div.checkbox input[type='checkbox'] + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  position: relative;
  left: -20px;
}

div.checkbox input[type='checkbox'] + label::before {
  content: '';
  display: inline-block;
  width: 1em;
  height: 1em;
  text-align: center;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #adb5bd;
  border-radius: 0.25em;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}

div.checkbox input[type='checkbox']:checked + label::before {
  background-color: #ffffff;
  background-image: url(checkmark.svg);
  background-size: 10px;
  /* content:  "\e0fe"; */
}

div.subCheckbox input[type='checkbox'] {
  position: relative;
  opacity: 0;
  width: 16px;
  height: 16px;
  z-index: 99;
}

div.subCheckbox input[type='checkbox'] + label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  position: relative;
  left: -20px;
}

div.subCheckbox input[type='checkbox'] + label::before {
  content: '';
  display: inline-block;
  width: 1em;
  height: 1em;
  text-align: center;
  flex-shrink: 0;
  flex-grow: 0;
  border: 1px solid #adb5bd;
  border-radius: 0.25em;
  margin-right: 0.5em;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 50% 50%;
}

div.subCheckbox input[type='checkbox']:checked + label::before {
  background-color: #ffffff;
  background-image: url(checkmark.svg);
  background-size: 10px;
}

div.checkbox input[type='checkbox']:indeterminate + label::before {
  background-color: #ffffff;
  background-image: url(svg/minus.svg);
  background-size: 10px;
}

input:focus {
  border: 1px solid #c9cbcc;
  border-radius: 3px;
